<template>
    <div>
      <!-- search Í¥ÄÎ†® div ÏãúÏûë -->
      <div class="col-md-8">
        <div class="input-group mb-3">
          <!--            Todo : ÏàòÏ†ï ÏãúÏûë -->
          <input
            type="text"
            class="form-control"
            placeholder="Search by Email"
            v-model="searchEmail"
          />
          <div class="input-group-append">
            <button
              class="btn btn-outline-secondary"
              type="button"
              @click="
                page = 1;
                retrieveCustomer();
              "
            >
              Search
            </button>
          </div>
          <!--            Todo : ÏàòÏ†ï ÎÅù -->
        </div>
      </div>
      <!-- search Í¥ÄÎ†® div ÎÅù -->
  
      <!--    Todo : page Î∞î ÏãúÏûë -->
      <div class="col-md-12">
        <div class="mb-3">
          Items per Page:
          <select v-model="pageSize" @change="handlePageSizeChange($event)">
            <option v-for="size in pageSizes" :key="size" :value="size">
              <!--            size : 3, 6, 9 -->
              {{ size }}
            </option>
          </select>
        </div>
  
        <b-pagination
          v-model="page"
          :total-rows="count"
          :per-page="pageSize"
          prev-text="Prev"
          next-text="Next"
          @change="handlePageChange"
        ></b-pagination>
      </div>
      <!--     Todo : page Î∞î ÎÅù -->
  
      <!--     Todo : ÌÖåÏù¥Î∏î Î™©Î°ù ÏãúÏûë -->
      <table class="table">
        <thead>
          <tr>
            <th scope="col">Name</th>
            <!-- <th scope="col">Last Name</th> -->
            <th scope="col">Email</th>
            <th scope="col">Phone</th>
            <th scope="col">Actions</th>
          </tr>
        </thead>
        <tbody v-for="(data, index) in customer" :key="index">
          <tr>
            <td>{{ data.lastName + " " + data.firstName }}</td>
            <!--  <td>{{ data.lastName }}</td> -->
            <td>{{ data.email }}</td>
            <td>{{ data.phone }}</td>
            <td>
              <!-- Í≥†Í∞ùÏ†ïÎ≥¥ ÏàòÏ†ï Î≤ÑÌäº -->
              <router-link :to="'/customer/' + data.cid"
                ><span class="badge bg-success">Edit</span></router-link
              >
            </td>
          </tr>
        </tbody>
      </table>
      <!--    Todo : ÌÖåÏù¥Î∏î Î™©Î°ù ÎÅù -->
    </div>
</template>
  
  
<script>
// üëÄaxios Í≥µÌÜµÌï®Ïàò(springboot Ïó∞ÎèôÏùÑ ÏúÑÌïú Ìï®ÏàòÎì§Ïùò Î™®ÏûÑ) import ÌïÑÏàò!!
import CustomerDataService from "../../services/CustomerDataService";

  export default {
    data() {
      return {
        customer: [],
        searchEmail: "", // Ïù¥Î©îÏùº Í≤ÄÏÉâ
  
        // ÌéòÏù¥ÏßïÏùÑ ÏúÑÌïú Î≥ÄÏàò Ï†ïÏùò
        page: 1,     // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ
        count: 0,    // Ï†ÑÏ≤¥ Îç∞Ïù¥ÌÑ∞ Í±¥Ïàò
        pageSize: 3, // ÌïúÌéòÏù¥ÏßÄÎãπ Î™áÍ∞úÎ•º ÌôîÎ©¥Ïóê Î≥¥Ïó¨Ï§ÑÏßÄ Í≤∞Ï†ïÌïòÎäî Î≥ÄÏàò
  
        pageSizes: [3,6,9] // select box Ïóê ÎÑ£ÏùÑ Í∏∞Î≥∏ Îç∞Ïù¥ÌÑ∞
      }
    },
    methods: {
      // Ìï®ÏàòÎ™Ö : retrieveCustomer()
      // Îß§Í∞úÎ≥ÄÏàò : this.searchEmail (Í≤ÄÏÉâÏñ¥), 
      //           this.page-1 (ÌòÑÏû¨ ÌéòÏù¥ÏßÄ Î≤àÌò∏)
      //           this.pageSize (ÌéòÏù¥ÏßÄÎãπ Ï∂úÎ†•Ìï† Îç∞Ïù¥ÌÑ∞Í∞ØÏàò)
      // Î¶¨ÌÑ¥Í∞í : ÏóÜÏùå
      retrieveCustomer() {
        CustomerDataService.getAll(this.searchEmail, this.page-1, this.pageSize)  // ÌîÑÏóî ÏûêÎèô ÌéòÏù¥ÏßïÏ≤òÎ¶¨Í∞Ä 1Î∂ÄÌÑ∞ ÏãúÏûëÌïòÎØÄÎ°ú -1Ìï¥ÏÑú 0ÏúºÎ°ú ÎßåÎì§Ïñ¥ Ï§å(Ïä§ÌîÑÎßÅÎ∂ÄÌä∏Îäî 0Î≤àÎ∂ÄÌÑ∞ Îß§Í∏∞ÎãàÍπå ÎßûÏ∂∞Ï§å)
        // axiosÏÑ±Í≥µÌïòÎ©¥ Ïä§ÌîÑÎßÅÎ∂ÄÌä∏ Í≤∞Í≥ºÍ∞Ä .then() ÏïàÏúºÎ°ú Ï†ÑÏÜ°Îê®
        .then((response) => {
          // const(let){ÏÜçÏÑ±Î™Ö, ÏÜçÏÑ±Î™Ö2} = Îç∞Ïù¥ÌÑ∞Í∞ùÏ≤¥Î∞∞Ïó¥  : Î™®ÎçòÏûêÎ∞îÏä§ÌÅ¨Î¶ΩÌä∏ Î¨∏Î≤ï Íµ¨Ï°∞Î∂ÑÌï¥Ìï†Îãπ
          const {customer, totalItems} = response.data; // springboot ÏóêÏÑú Ï†ÑÏÜ°Îêú Îßµ(map) Ï†ïÎ≥¥
          this.customer = customer;         // Ïä§ÌîÑÎßÅ Î∂ÄÌä∏ÏóêÏÑú Ï†ÑÏÜ°Ìïú Îç∞Ïù¥ÌÑ∞
          this.count = totalItems;  // Ïä§ÌîÑÎßÅÎ∂ÄÌä∏ÏóêÏÑú Ï†ÑÏÜ°Ìïú ÌéòÏù¥ÏßÄÏ†ïÎ≥¥(Ï¥ù Í±¥Ïàò)
          // ÎîîÎ≤ÑÍπÖ ÏΩòÏÜîÏóê Ï†ïÎ≥¥ Ï∂úÎ†•
          console.log(response.data);
        })
        // Ïã§Ìå®ÌïòÎ©¥ .catch() Ïóê ÏóêÎü¨Í∞Ä Ï†ÑÏÜ°Îê®
        .catch((e) => {
          console.log(e);
        });
      },
      // select box Í∞í Î≥ÄÍ≤ΩÏãú Ïã§ÌñâÎêòÎäî Ìï®Ïàò(Ïû¨Ï°∞Ìöå)üí´
      // event : ÏÇ¨Ïö©ÏûêÍ∞Ä ÌÅ¥Î¶≠Ìïú Ïª®Ìä∏Î°§Ïù¥ Ïù¥Î≤§Ìä∏Í∞ùÏ≤¥Î•º ÌÜµÌï¥ Ï†ëÍ∑º,ÌôïÏù∏Í∞ÄÎä•
      handlePageSizeChange(event) {
        this.pageSize = event.target.value    // ÏÖÄÎ†âÌä∏Î∞ïÏä§ Í∞í Í∞ÄÏ†∏Ïò§Í∏∞ -> Ìïú ÌéòÏù¥ÏßÄÎãπ Ï∂úÎ†•Ìï† Îç∞Ïù¥ÌÑ∞ Í∞ØÏàò Ï†ÄÏû•
        this.page = 1;    // ÌòÑÏû¨ÌéòÏù¥ÏßÄÎ•º Ï≤´ ÌéòÏù¥ÏßÄÎ°ú ÏÑ∏ÌåÖ
        // Ïû¨Ï°∞ÌöåÌï®Ïàò Ìò∏Ï∂ú
        this.retrieveCustomer();
      },
      // ÌéòÏù¥ÏßÄÎ≤àÌò∏ Î≥ÄÍ≤Ω Ïãú Ïã§ÌñâÎêòÎäî Ìï®Ïàò(Ïû¨Ï°∞Ìöå)üî±
      handlePageChange(value) {
        // Îß§Í∞úÎ≥ÄÏàòÍ∞íÏúºÎ°ú ÌòÑÏû¨ ÌéòÏù¥ÏßÄÎ≤àÌò∏ Î≥ÄÍ≤Ω
        this.page = value;
        // Ïû¨Ï°∞Ìöå Ìï®Ïàò Ìò∏Ï∂ú
        this.retrieveCustomer();
      }
    },
    // üëÄÌôîÎ©¥Ïù¥ Îú®ÏûêÎßàÏûê Ïã§ÌñâÎêòÎäî Ïù¥Î≤§Ìä∏(ÎùºÏù¥ÌîÑ ÏÇ¨Ïù¥ÌÅ¥ Ìï®Ïàò) : mounted(), created()
    mounted() {
        this.retrieveCustomer(); // üëÄÌôîÎ©¥ Î°úÎî©Ïãú Ï†ÑÏ≤¥ Ï°∞ÌöåÌï®Ïàò retrieveCustomer() Ìï®Ïàò Ïã§Ìñâ
    },
  
  }
</script>
  
